@font-face {
  font-family: "Roboto Condensed";
  src: local("Roboto Condensed"), local("Roboto-Condensed"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "BebasNeueCyrillic";
  src: url("../fonts/BebasNeueCyrillic.ttf") format("truetype"), url("../fonts/BebasNeueCyrillic.woff2") format("woff2"), url("../fonts/BebasNeueCyrillic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Bebas Neue";
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2"), url("../fonts/BebasNeueRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Roboto Condensed", arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #102654; }

.overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

img {
  max-width: 100%;
  height: auto; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

.header {
  background-image: url("../img/background-header.png");
  background-repeat: no-repeat;
  background-size: cover; }

.header__contact {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-left: 103px;
  padding-right: 103px; }
  @media (max-width: 1023px) {
    .header__contact {
      padding-top: 39px;
      padding-left: 44px;
      padding-right: 46px; } }
  @media (max-width: 767px) {
    .header__contact {
      padding-top: 18px;
      padding-left: 15px;
      padding-right: 15px; } }

.header__contact_logo,
.header__contact_phone {
  font-family: "Bebas Neue", arial, sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.02em;
