      padding-bottom: 78px;
      margin-right: 15px;
      margin-left: 15px; } }

.header__info {
  display: flex;
  flex-direction: column;
  padding-right: 74px;
  margin-left: auto;
  padding-left: 10px; }
  @media (max-width: 1023px) {
    .header__info {
      margin: 0;
      padding: 0;
